import axios from "axios";
import store from "../store";
import { notify, close } from "../../components/MLNotify/controls";

export const setLoadingBarProgress = (value) => ({
  type: "SET_PROGRESS",
  payload: value,
});

export const updatetotalproducts = () => ({
  type: "UPDATE_TOTAL_PRODUCTS_AA",
});

export const getStore = () => (dispatch) => {
  dispatch({
    type: "SET_PROGRESS",
    payload: 0,
  });

  dispatch({
    type: "SET_PROGRESS",
    payload: 57,
  });
  axios
    .get("/dash/store")
    .then((data) => {
      dispatch({
        type: "GET_STORE_DEFAULTS",
        payload: data.data,
      });
      dispatch({
        type: "SET_PROGRESS",
        payload: 100,
      });
      dispatch({ type: "DASH_READY" });
      // console.log(data.data);
    })
    .catch((err) => {
      // console.log(err);
    });
};

export const dashoverlay = (n) => (dispatch) => {
  dispatch({
    type: "DASH_OVERLAY_ACTIVE",
    payload: n,
  });
};

export const createProduct = () => (dispatch) => {
  let product = store.getState().product;
  let productVariations = store.getState().productAR.variations;
  let productSpecs = store.getState().productAR.specifications;

  let finalProduct = {};

  let productSpecsObject = {};
  let specNameObject = [];

  productSpecs.forEach((x) => {
    productSpecsObject = {
      ...productSpecsObject,
      [x.sname
        .toLowerCase()
        .replace(/ /g, "_")]: x.svalue.toLowerCase().replace(/ /g, "_"),
    };
    specNameObject.push(x.sname.toLowerCase().replace(/ /g, "_"));
  });

  finalProduct = {
    ...product,
    ...productSpecsObject,
    productSpecsFields: specNameObject,
  };

  // console.group("product creation");
  // console.log(finalProduct);
  // console.groupEnd();

  if (product.productName === "") {
    return notify(
      2,
      "Invalid name",
      "",
      "All products must have a suitable name so that customers can easily find what they looking for"
    );
  }
  if (product.productPrice === "" || product.productPrice === "0") {
    return notify(
      2,
      "Invalid price",
      "",
      "Products are required to have a suitable pricing. Free products are not allowed in Mallorar. See mallorar policy"
    );
  }
  if (product.esShippingDates === "" || product.esShippingDates === "0") {
    return notify(
      2,
      "Invalid shipping dur...",
      "",
      "We require sellers to provide an estimation to the possible shipping duration taken to deliver a product to your furthest shipping zone"
    );
  }
  if (product.productImg === "default") {
    return notify(
      2,
      "No product image",
      "",
      "All products are required to have a product image so that customers can see what they buying"
    );
  }
  if (product.productDepartment === "") {
    return notify(
      2,
      "Invalid Department",
      "",
      "Assign your product to a suitable department to increase performance"
    );
  }
  if (product.productCategory === "") {
    return notify(
      2,
      "Invalid Category",
      "",
      "Assign your product to a suitable category to increase performance"
    );
  }
  if (product.childCategry === "") {
    return notify(
      2,
      "Invalid Sub category",
      "",
      "Assign your product to a suitable sub category to better place your product"
    );
  }

  close();

  store.dispatch({ type: "DASH_OVERLAY_ACTIVE", payload: true });

  let productID;
  axios
    .post(`/dash/product/add`, {
      productData: finalProduct,
    })
    .then((data) => {
      store.dispatch({
        type: "SET_PRODUCT_ID",
        payload: data.data.productID,
      });

      productID = data.data.productID;

      if (productVariations.length > 0) {
        return productVariations.map((x) => {
          axios.post("/dash/product/add/variable", {
            productId: data.data.productID,
            variableName: x.varibleName,
            variableValue: x.variables,
          });
        });
      }
    })
    .then(() => {
      store.dispatch({ type: "DASH_OVERLAY_ACTIVE", payload: false });
      store.dispatch({ type: "MLPC", payload: true });
      return notify(
        1,
        `Product Created`,
        finalProduct.productImg,
        `Product successfully created with ID ${productID}. Your product will be spread around Mallorar immediately`
      );
    })
    .catch((err) => {
      // console.log(err);
    });
};

